import React, { Component } from "react";
import Loader from "Components/Loader";
import styled from "styled-components";
import Section from "Components/Section";
import Message from "Components/Message";

const Container = styled.div`
  padding: 20px;
`;

const HomePresenter = ({
  Ccategory,
  certification,
  Jcategory,
  error,
  loading,
}) => (
  <>
    {loading ? (
      <Loader />
    ) : (
      <Container>
        <Section title="Certification Category">
          {Ccategory.map((Ccategory) => (
            <Section>{Ccategory.CATEGORY}</Section>
          ))}
        </Section>
        <Section title="Certification">
          {certification.map((certification) => (
            <Section>{certification.NAME}</Section>
          ))}
        </Section>
        <Section title="Jcategory">
          {Jcategory.map((Jcategory) => (
            <Section>{Jcategory.NAME}</Section>
          ))}
        </Section>
        {error && <Message color="#e74c3c" text={error} />}
      </Container>
    )}
  </>
);

export default HomePresenter;
